// https://ckeditor.com/docs/ckeditor5/latest/framework/guides/creating-simple-plugin.html
// https://github.com/ckeditor/ckeditor5-alignment/blob/894745ecb1e8bd94286b4089eb16079034eb8a0b/src/alignmentui.js#L107-L124
import Plugin from '@ckeditor/ckeditor5-core/src/plugin';
import { createDropdown, addListToDropdown } from '@ckeditor/ckeditor5-ui/src/dropdown/utils';
import Collection from '@ckeditor/ckeditor5-utils/src/collection';
import Model from '@ckeditor/ckeditor5-ui/src/model';

const items = new Collection();

items.add( {
	type: 'button',
	model: new Model( {
		withText: true,
		label: 'First item',
		labelStyle: 'color: red'
	} )
} );

items.add( {
	type: 'text',
	model: new Model( {
		withText: true,
		label: 'Second item',
		labelStyle: 'color: green',
		class: 'foo'
	} )
} );

export default class extends Plugin {
	static get pluginName() {
		return 'Placeholders';
	}

	init() {
		// eslint-disable-next-line no-undef
		console.log( 'Placeholders was initialized' );
		const editor = this.editor;
		const options = editor.config.get( 'placeholders' );

		console.log( options );

		editor.ui.componentFactory.add( 'placeholders', locale => {
			const dropdownView = createDropdown( locale );
			dropdownView.extendTemplate( {
				attributes: {
					class: 'ck--placeholders-dropdown'
				}
			} );

			dropdownView.buttonView.isHorizontal = true;

			dropdownView.buttonView.set( {
				label: options.label,
				withText: true,
				tooltip: true
			} );

			dropdownView.buttonView.on( 'execute', () => {
				// Add the behavior of the "action part" of the split button.
				// Split button consists of the "action part" and "arrow part".
				// The arrow opens the dropdown while the action part can have some other behavior.
			} );

			// Callback executed once the image is clicked.
			// view.on( 'execute', () => {
			// 	const imageURL = prompt( 'Image URL' );
			// } );
			addListToDropdown( dropdownView, items );
			dropdownView.render();
			return dropdownView;
		} );
	}

	_addButton( option ) {
		const editor = this.editor;

		editor.ui.componentFactory.add( `alignment:${ option }`, locale => {
			const command = editor.commands.get( 'alignment' );
			const buttonView = new ButtonView( locale );

			buttonView.set( {
				label: this.localizedOptionTitles[ option ],
				icon: icons.get( option ),
				tooltip: true
			} );

			// Bind button model to command.
			buttonView.bind( 'isEnabled' ).to( command );
			buttonView.bind( 'isOn' ).to( command, 'value', value => value === option );

			// Execute command.
			this.listenTo( buttonView, 'execute', () => {
				editor.execute( 'alignment', { value: option } );
				editor.editing.view.focus();
			} );

			return buttonView;
		} );
	}
}
